<template>
  <div>
    <div>
      <div class="has-margin-top-7 has-margin-bottom-7">
        <div class="is-flex-space-between has-text-grey is-size-7">
          <span>{{ forecastStart }}</span>
          <span>{{ forecastHalf }}</span>
          <span>{{ forecastEnd }}</span>
        </div>
        <div class="is-size-7-mobile">
          <ul class="rain-forecast">
            <li v-for="(rainForecast) in rainForecasts" :key="rainForecast.id" :title="rainForecast.title" class="rain-forecast-item has-text-centered" :class="`is-rain-${rainForecast.value}`">
              <i :class="getRainIconClass(rainForecast.value)" />
            </li>
          </ul>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import { createNamespacedHelpers } from 'vuex'
const { mapGetters } = createNamespacedHelpers('data')

export default {
  props: {
    equipment: {
      type: Object,
      required: true,
    },
  },
  computed: {
    // global
    states: function () {
      const states = []
      this.equipment.states.forEach((stateId) => states.push(this.getStateById(stateId)))
      return states.filter((state) => state.isVisible).sort((a, b) => a.order - b.order)
    },
    ...mapGetters(['getStateById']),
    // rainForecast
    rainForecasts: function () {
      const rainForecasts = []
      const forecastDate = this.getStateValueByLogicalId('forecastDate')
      for (let i = 0; i < 12; i++) {
        const state = this.states.find((state) => state.logicalId === 'prev' + i)
        const title = this.getFormattedTimeFromHourMinuteText(forecastDate, i * 5) + ' Ã  ' + this.getFormattedTimeFromHourMinuteText(forecastDate, (i + 1) * 5)
        rainForecasts.push({ id: state.logicalId, title, value: state.currentValue })
      }
      return rainForecasts
    },
    forecastStart: function () { return this.getFormattedTimeFromHourMinuteText(this.getStateValueByLogicalId('forecastDate')) },
    forecastHalf: function () { return this.getFormattedTimeFromHourMinuteText(this.getStateValueByLogicalId('forecastDate'), 30) },
    forecastEnd: function () { return this.getFormattedTimeFromHourMinuteText(this.getStateValueByLogicalId('forecastDate'), 60) },
  },
  methods: {
    // global
    getStateValueByLogicalId (logicalId) {
      const state = this.states.find((state) => state.logicalId === logicalId)
      if (!state) {
        return
      }
      return state.currentValue || null
    },
    getRainIconClass (value) {
      switch (parseInt(value)) {
        case 1:
          return 'fa fa-cloud-sun'
        case 2:
        case 3:
          return 'fa fa-cloud-rain'
        case 4:
          return 'fa fa-cloud-showers-heavy'
        default:
          return 'fa fa-question'
      }
    },
    getFormattedTimeFromHourMinuteText (text, minutesToAdd = 0) {
      if (!text) {
        return ''
      }
      return this.$moment(text).add(minutesToAdd, 'minutes').format('LT')
    },
  },
}
</script>
