  border-radius: 290486px 0 0 290486px;
}
.rain-forecast .rain-forecast-item:last-child {
  border-radius: 0 290486px 290486px 0;
}
.is-rain-1 {
  color: hsl(48, 100%, 40%);
  background-color: hsl(48, 100%, 67%);
}
.is-rain-2 {
  color: hsl(204, 86%, 40%);
  background-color: hsl(204, 86%, 75%);
}
.is-rain-3 {
  color: hsl(204, 86%, 32%);
  background-color: hsl(204, 86%, 64%);
}
.is-rain-4 {
  color: hsl(204, 86%, 24%);
  background-color: hsl(204, 86%, 53%);
}
</style>
